<template>
	<view class="page">
		<view class="item" v-for="(res, index) in siteList" :key="res.id">
			<view class="flex">
				<view class="top">
					<view class="top-flex">
						<view class="name">{{ res.receiverName }}</view>
						<view class="phone">{{ maskPhoneNumber(res.phone) }}</view>
						<view class="tag">
							<text v-for="(item, index) in res.tag" :key="index"
								:class="{red:item.tagText=='默认'}">{{ item.tagText }}</text>
						</view>
					</view>
					<view class="site">
						{{res.province}}{{res.city}}{{res.area}}{{res.detailAddress}}
					</view>
				</view>
				<view class="bottom">
					<u-icon name="edit-pen" :size="40" color="#999999" @click="toAddSite(res)"></u-icon>
					<u-icon name="trash" :size="40" color="#999999" @click="deleSite(res)" style="margin-left: 30rpx;"></u-icon>
				</view>
			</view>
		</view>
		<view class="addSite" @click="toAddSite">
			<view class="add">
				<u-icon name="plus" color="#ffffff" class="icon" :size="30"></u-icon>新增收货地址
			</view>
		</view>
		<u-empty v-if="siteList.length <= 0" text="暂无收货地址" mode="siteList"></u-empty>
	</view>
</template>

<script>
	// import {
	//     getUserAddressList,
	// 	addressDel
	// } from "@/api/address.js";
	export default {
		data() {
			return {
				siteList: [{
					receiverName: '马潇',
					phone: '13888888888',
					tag: [{
						tagText: '默认'
					}],
					province: '陕西省',
					city: '西安市',
					area: '雁塔区',
					detailAddress: '中华世纪城'
				}]
			};
