				// 		this.siteList.forEach(item => {
				// 			if (item.isDefault == 1) {
				// 				item.tag = [{
				// 					tagText: '默认'
				// 				}]
				// 			}
				// 		})
				// 	}
				// })
			},
			toAddSite(item){
				console.log(item, 'item')
				this.go({
