				    params: item
				})
			},
			deleSite(item){
				console.log(item, 'item')
				if (item.isDefault == 1) {
					this.$u.toast('不能删除默认地址');
					return false
				}
				uni.showModal({
					title: '删除',
					content: '您确定要删除此地址吗？',
