						// if (res.confirm) {
						// 	const params = {
						// 		ids: item.id
						// 	}
						// 	addressDel(params).then(res => {
						// 		if (res.data) {
						// 			this.$u.toast('删除成功');
						// 			this.getData();
						// 		}
						// 	})
						// }
					}
				});
			},
			maskPhoneNumber(phoneNumber) {
			    return phoneNumber.replace(/(\d{3})\d{3}(\d{4})/, '$1***$2');
			},
		}
	}
</script>

