								width: 60rpx;
								height: 34rpx;
								line-height: 34rpx;
								color: #ffffff;
								font-size: 20rpx;
								border-radius: 6rpx;
								text-align: center;
								margin-left: 30rpx;
								background-color: rgb(49, 145, 253);
							}
										
							.red {
								background-color: red
							}
						}
					}
					.site {
